import React from "react";
import { Col, Row } from "react-bootstrap";

function WhatIKnow() {
  return (
    <Row>
      <h1 className="text-center mb-5 mt-3">What I Know?</h1>
      <Col md={3}>
        <h4>Languages</h4>
        <ul>
          <li>C</li>
          <li>Java</li>
          <li>Python</li>
          <li>JavaScript</li>
        </ul>
      </Col>
      <Col md={3}>
        <h4>Libraries</h4>
        <ul>
          <li>React</li>
          <li>Redux</li>
        </ul>
      </Col>
      <Col md={3}>
        <h4>Frameworks</h4>
        <ul>
          <li>Laravel</li>
          <li>Django</li>
        </ul>
      </Col>
      <Col md={3}>
        <h4>Databases</h4>
        <ul>
          <li>Sqlite</li>
          <li>MySql</li>
          <li>MongoDB</li>
        </ul>
      </Col>
    </Row>
  );
}

export default WhatIKnow;
