import React, { useState } from "react";
import { Col, Row, Container } from "react-bootstrap";

function RockPaperScissors() {
  const [userChoise, setuserChoise] = useState(null);
  const [comChoise, setcomChoise] = useState(null);
  function SetChoise(choise) {
    setuserChoise(choise);
    const array = ["Rock", "Paper", "Scissor"];
    const random = Math.floor(Math.random() * array.length);
    setcomChoise(array[random]);
  }
  function Winner() {
    if (userChoise === comChoise) {
      return 0;
    }

    if (userChoise === "Rock") {
      if (comChoise === "Scissor") {
        return 1;
      } else {
        return -1;
      }
    }

    if (userChoise === "Paper") {
      if (comChoise === "Rock") {
        return 1;
      } else {
        return -1;
      }
    }

    if (userChoise === "Scissor") {
      if (comChoise === "Paper") {
        return 1;
      } else {
        return -1;
      }
    }
  }
  return (
    <>
      <Container>
        <h1 className="text-center py-3">Make a Choice</h1>
        <Row>
          <Col md={4} className="p-5">
            <img
              src="/images/rock.png"
              alt="Rock"
              width="100%"
              className="game-object"
              onClick={() => SetChoise("Rock")}
            />
          </Col>
          <Col md={4} className="p-5">
            <img
              src="/images/paper.png"
              alt="Paper"
              width="100%"
              className="game-object"
              onClick={() => SetChoise("Paper")}
            />
          </Col>
          <Col md={4} className="p-5">
            <img
              src="/images/scissor.png"
              alt="Scissor"
              width="100%"
              className="game-object"
              onClick={() => SetChoise("Scissor")}
            />
          </Col>
        </Row>
        <hr />
        {(() => {
          switch (Winner()) {
            case 0:
              return (
                <>
                  <p>Your Choise : {userChoise}</p>
                  <p>Computer Choise : {comChoise}</p>
                  <h1 className="text-center text-warning">Game Draw!!</h1>
                </>
              );
            case 1:
              return (
                <>
                  <p>Your Choise : {userChoise}</p>
                  <p>Computer Choise : {comChoise}</p>
                  <h1 className="text-center text-success">
                    You Won the game!!
                  </h1>
                </>
              );
            case -1:
              return (
                <>
                  <p>Your Choise : {userChoise}</p>
                  <p>Computer Choise : {comChoise}</p>
                  <h1 className="text-center text-danger">
                    You Lose the game!!
                  </h1>
                </>
              );
            default:
              return;
          }
        })()}
      </Container>
    </>
  );
}

export default RockPaperScissors;
