import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import FeedbackForm from "./FeedbackForm";
import SocialLinks from "./SocialLinks";
import WhatIKnow from "./WhatIKnow";

function About() {
  let profileImage = "https://source.unsplash.com/1600x900/?profile,men";

  return (
    <>
      <Container>
        <Row>
          <Col md={6} className="pt-5">
            <img src={profileImage} alt="" width="100%" />
          </Col>
          <Col md={6} className="pt-5">
            <h3 className="mb-2">Lorem ipsum dolor sit amet!</h3>
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Repellat
              debitis perspiciatis odio eaque temporibus sunt porro in, ullam
              reprehenderit provident sapiente asperiores cum incidunt numquam
              reiciendis beatae perferendis et accusantium.
            </p>
            <hr />
            <SocialLinks />
            <hr />
          </Col>
        </Row>
        <hr />
        {/* <FeedbackForm /> */}
        <WhatIKnow />
      </Container>
    </>
  );
}

export default About;
